import React from 'react'
import './jbook.module.css'
import image from '../assets/jbook.png'
import notes from "../assets/notes.png"
import error from "../assets/error.png"
import Github from '../components/Github'
export default function Jbook() {
    return (
        <>
        <section className="hero  is-fullheight ">
      <div className="hero-body">
        <div className="hero">
          <div className="columns">
            <div className="column is-half">
              <p className="title">
                Jbook
              </p>
              <p className="paragraph">
                Jupyter style notebook for javascript
                <span
                  >with in-browser bundling capabilities that allows the
                  importing, test,</span
                >
                and execution of javascript in a safe environment for
                development, build using:
              </p>
              <ul>
                <li><span> TypesScript </span></li>
                <li><span>ESBUILD</span></li>
                <li><span>Redux/toolkit</span></li>
              </ul>
            </div>
            <div className="column is-half center ">
              <img src={image} className="imgwidth extras" alt={image}/>
              <div className='github'>
             <Github  url="https://github.com/carlosmpr/jbook"/>
             </div>
            </div>
          </div>
        </div>
      </div>
    </section>

    <section className="hero  is-fullheight padding">
      <div style={{marginBottom:'50px'}}>
        <div className="hero">
          <p className="title">
            Features
          </p>
          <div className="columns">
            <div className="column is-one-third jbookImg ">
              <img src={image} className="imgwidth  extras " alt={image}/>
             
               <button className="button is-primary move">Add or remove code</button>
            </div>

            <div className="column is-one-third center jbookImg">
              <img src={notes} className="imgwidth extras" alt={notes}/>
              <button className="button is-primary move">Live text editor</button>
            </div>

            <div className="column is-one-third center jbookImg">
              <img src={error} className="imgwidth extras"  alt={error}/>
              <button className="button is-primary move">Real time error check</button>
            </div>
          </div>
        </div>
      </div>
    </section>
    </>
    )
}
