import React, { useState } from "react";
import "./connect.css";
import * as sanitizeHtml from "sanitize-html";
import * as EmailValidator from "email-validator";
import Github from "../components/Github";
import axios from "axios";

export default function Connect() {
  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const [message, setMessage] = useState("");
  const [error, setError] = useState(false);
  const [success, setSucces] = useState(false);

  const sendMessage = async () => {
    const newEmail = sanitizeHtml(email);
    const newName = sanitizeHtml(name);
    const newMessage = sanitizeHtml(message);

    if (EmailValidator.validate(email) && message !== "") {
      try {
        await axios.post(
          "https://rk82vfsokc.execute-api.us-east-1.amazonaws.com/dev/mailer",
          {
            email: newEmail,
            name: newName,
            message: newMessage,
          }
        );
        setSucces(true);
      } catch (_) {
        setError(true);
      }
    } else {
      setError(true);
    }
  };

  return (
    <section className="hero  is-fullheight">
      <div className="hero-body">
        <div className="hero">
          <div className="columns">
            <div className="column is-half ">
              <p className="title">Connect with me</p>
              {success ? (
                <div>
                  <div className="notification is-success">
                    Message sent successfully!
                  </div>
                </div>
              ) : (
                <div>
                  <div className="inputControl">
                    <label>Email</label>
                    <input
                      className="input is-primary"
                      type="text"
                      placeholder="Email"
                      value={email}
                      name={email}
                      onChange={(e) => {
                        setError(false);
                        setEmail(e.target.value);
                      }}
                    />
                  </div>
                  <div className="inputControl">
                    <label>Name</label>
                    <input
                      className="input is-primary"
                      type="text"
                      placeholder="Name"
                      value={name}
                      name={name}
                      onChange={(e) => {
                        setError(false);
                        setName(e.target.value);
                      }}
                    />
                  </div>
                  <div className="inputControl">
                    <label>Message</label>
                    <textarea
                      className="textarea is-primary"
                      placeholder="Message"
                      value={message}
                      name={message}
                      onChange={(e) => {
                        setMessage(e.target.value);
                      }}
                    ></textarea>
                  </div>

                  {error ? (
                    <div className="notification is-danger">
                      Please verify Email and Message!
                    </div>
                  ) : null}

                  <button
                    className="button is-primary sendButton"
                    onClick={sendMessage}
                  >
                    Send Email
                  </button>
                </div>
              )}
            </div>
            <div className="column is-half infoButtons ">
              <a
                href="https://drive.google.com/file/d/1Z5jz6_FNW-RgqVFZQQOI6vwcCGAFC3ui/view?usp=sharing"
                className="button is-danger is-outlined is-large"
                download
              >
                <span className="icon is-medium">
                  <i className="far fa-file-alt"></i>
                </span>
                <span>Resume</span>
              </a>
              <Github url="https://github.com/carlosmpr" />
              <a
                href="https://www.linkedin.com/in/carlos-polanco-35b686123/"
                className="button is-info is-outlined is-large"
              >
                <span className="icon is-medium">
                  <i className="fab fa-linkedin"></i>
                </span>
                <span>Linkedin</span>
              </a>
              <a
                href="https://cmpr.medium.com/"
                className="button is-black is-outlined is-large"
              >
                <span className="icon is-medium">
                  <i className="fab fa-medium"></i>
                </span>
                <span>Medium</span>
              </a>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
