import React from "react";
import './pedigreeBreading.css'
import breed3 from "../assets/bree3.png"
import breed2 from "../assets/breed2.png"
import breed from "../assets/breed.png"
import breed5 from "../assets/bree5.png"
export default function PedigreeBreading() {
  return (
    <div className="white">
      <section className="hero white  is-fullheight breederheigh"  >
        <div className="padding" style={{position:'relative'}}>
          <div className="hero">
            <p className="title">PedigreeBreading</p>
            <div className="card3">
              <img src={breed3} className="imgwidth" alt={breed3} />
            </div>
            <div className="card2">
              <img src={breed2} className="imgwidth" alt={breed2}/>
            </div>
            <div className="cardB">
              <img src={breed} className="imgwidth" alt={breed}/>
            </div>
            <div className="content">
              <p>
                Develop with Flutter natively compiled applications for IOS,
                Android, web, desktop, and embedded devices from a single
                codebase.
              </p>
            </div>
          </div>
        </div>
      </section>
      <section className="hero white is-fullheight" >
        <div style={{marginBottom:'20px'}}>
          <div className="padding">
            <div className="columns ">
              <div className="column is-two-fifths">
                <p className="paragraph">
                  PedigreeBreading is an app that allows dog owners to find and
                  connect with other dogs owner, integrated with live chat,
                  training videos and more.
                </p>
                <p className="paragraph">
                  With Serverless architecture, featuring Aws Cognito for manage
                  Sign up and Sign in access, API Gateway and Lambda functions
                  for app logic and functionality, and dynamoDb for the data
                  store.
                </p>
              </div>
              <div className="column is-half " >
                <img src={breed5} className="imgwidth" alt={breed5}/>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}
