import React from 'react'
import './home.css'
import typesS from '../assets/typescript.svg'
import mySvg from '../assets/react.svg'
import next from "../assets/nextjs.svg"
import vue from "../assets/vuejs.svg"
import flutter from "../assets/flutter.svg"
import node from "../assets/node.svg"
import dart from "../assets/dart.svg"
import python from "../assets/python.svg"
import ruby from "../assets/ruby.svg"
import graphql from "../assets/graphql.svg"
import firebase from"../assets/firebase.svg"
import aws from "../assets/aws.svg"
import certification from '../assets/aws-certified-solutions-architect-associate.png'
import cert from '../assets/aws-cre.png'
import linux from "../assets/linux.png"
import cisco from "../assets/ccna.png"

export default function Home() {
    return (
        <section className="hero is-fullheight">
        <div className="hero-body">
          <div className="hero">
            <div className="columns">
              <div className="column is-half middle">
                <h1 className="title">
                  Carlos Polanco
                </h1>
    
                <p className="subtitle">
                  Sofware Developer
                </p>
              </div>
    
              <div className="column is-half ">
                <div className="columns center is-mobile">
                  <div className="column is-one-fifth">
                    <figure>
                      <img src={typesS} className="perspective"  alt={typesS}/>
                     
                    </figure>
                  </div>
                  <div className="column is-one-fifth">
                    <figure>
                      <img src={mySvg} className="perspective" alt={mySvg} />
                     
                    </figure>
                  </div>
                  <div className="column is-one-fifth ">
                    <figure className="bigger white">
                      <img src={next} className="perspective" alt={next}/>
                      
                    </figure>
                  </div>
    
                  <div className="column is-one-fifth">
                    <figure>
                      <img src={vue} className="perspective"  alt={vue}/>
                      
                    </figure>
                  </div>
                </div>
                <div className="columns center is-mobile">
                  <div className="column is-one-fifth">
                    <figure>
                      <img src={flutter} className="perspective" alt={flutter}/>
                      
                    </figure>
                  </div>
    
                  <div className="column is-one-fifth">
                    <figure className="bigger">
                      <img src={node} className="perspective" alt={node}/>
                     
                    </figure>
                  </div>
                  <div className="column is-one-fifth">
                    <figure className="bigger">
                      <img src={dart} className="perspective" alt={dart}/>
                    
                    </figure>
                  </div>
                  <div className="column is-one-fifth">
                    <figure>
                      <img src={python} className="perspective" alt={python}/>
                      
                    </figure>
                  </div>
                </div>
                <div className="columns center is-mobile">
                  <div className="column is-one-fifth">
                    <figure>
                      <img src={ruby} className="perspective" alt={ruby}/>
                      
                    </figure>
                  </div>
                  <div className="column is-one-fifth">
                    <figure>
                      <img src={graphql} className="perspective" alt={graphql}/>
                     
                    </figure>
                  </div>
                  <div className="column is-one-fifth">
                    <figure>
                      <img src={firebase} className="perspective" alt={firebase}/>
                      
                    </figure>
                  </div>
                  <div className="column is-one-fifth ">
                    <figure className="bigger white">
                      <img src={aws} className="perspective"  alt={aws}/>
                     
                    </figure>
                  </div>
                </div>
    
                <div className="columns center is-mobile">
                  <div className="column is-one-fifth">
                    <figure className="bigger">
                      <img
                        src={certification}
                        className="perspective"
                        alt="aws"
                      />
                    </figure>
                  </div>
                  <div className="column is-one-fifth">
                    <figure className="bigger">
                      <img src={cert} className="perspective"  alt="cloud"/>
                    </figure>
                  </div>
                  <div className="column is-one-fifth">
                    <figure className="bigger">
                      <img src={linux} className="perspective" alt="linux"/>
                    </figure>
                  </div>
                  <div className="column is-one-fifth ">
                    <figure className="bigger">
                      <img src={cisco} className="perspective" alt="cisco"/>
                    </figure>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    )
}
