import React from "react";
import globo from "../assets/globod.png";
import architss from "../assets/architss.png";
import "./Websites.css";
export default function Websites() {
  return (
    <div>
      <section className="hero  is-fullheight website">
        <div className="hero-body " style={{marginBottom:'80px'}}>
          <div className="hero padding">
            <div className="columns">
              <div className="is-one-fifth">
                <p className="title">Websites</p>
                <p className="paragraph">
                  Modern and stylish websites
                  <span>build with Material Ui and Ant design </span>
                  With serverless backend to serve million of users
                </p>
              </div>
              <div className="is-one-fifth"></div>
              <div className="column is-one-third">
                <div className="card">
                  <div className="card-image">
                    <img src={architss} alt={architss} />
                  </div>
                  <a href="https://architss.net/" className="button is-danger">
                    Visit site
                  </a>
                </div>
              </div>
              <div className="column is-one-third">
                <div className="card">
                  <div className="card-image">
                    <img src={globo} alt={globo} />
                  </div>
                  <a href="https://globodebate.com/" className="button is-info">
                    Visit site
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}
